*{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.3rem;
  color:gray;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  height: 350px;
  width: 300px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  height: 300px;
  width: 300px;
}